import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/TimeRaiders/image1.jpeg"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Time Raiders</h1>
        
        <img class="timeraiders" src={image1} alt="image1"/>

        <div>
          <p>
          Time Raiders is a fast-paced shoot and loot, time travel game. Players travel through time and fight enemies to find loot. They can use this precious loot to power up their characters and weapons, craft it into new items, or sell it.
          The game is owned by Utopian Game Labs and I have worked on it while part of the Tractor, Set, Go! team as an external gameplay programmer. I have worked on multiple aspects of the gameplay, but mainly on abilties, weapons and vehicles and I have made slight refactors to AI behaviours and server logic.
          <br></br>
          <a href="https://www.youtube.com/watch?v=ed2trrqsXZo">Time Raiders NFT Pre-Sale Launch Trailer</a>
          <br></br>
          <a href="https://timeraiders.io">Time Raiders website</a>
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage